import { StandardPageLayout } from 'shared/layouts';
import { useRouter, useUnreadCaseTab } from 'shared/hooks';
import { Helmet } from 'react-helmet';
import { CustomMuiTabs, ErrorBoundary, Loader } from 'shared/components';
import { Link, Route, Switch } from 'react-router-dom';
import React, { useEffect, useState, ChangeEvent, Suspense, lazy } from 'react';
import { BusinessProfileHeader } from './components';
import { useTranslation } from 'react-i18next';

const BusinessProfilePage = lazy(() => import('./components/BusinessProfile'));
const Configuration = lazy(() => import('./components/Configuration'));
const Branches = lazy(() => import('./components/Branches'));
const FeedbackConfigurationPage = lazy(() => import('./components/Feedback'));

type Props = {
	match: {
		params: {
			clientType: string;
		};
		url: string;
		path: string;
	};
};

function a11yProps(index: number) {
	return {
		id: `simple-tab-${index}`,
		'aria-controls': `simple-tabpanel-${index}`
	};
}

function BusinessProfileMainInner({ match }: Props) {
	const { t } = useTranslation();
	const { unreadCasesQuantity, isHidden } = useUnreadCaseTab('User Profile');
	const [primeTab, setPrimeTab] = useState(0);
	const { location } = useRouter();
	const isConfigurationPage = location.pathname.includes('configuration');
	const isBranchPage = location.pathname.includes('branch');
	const isFeedbackConfigPage = location.pathname.includes('feedback');
	const { url, path } = match;
	const handleChangePrime = (event: ChangeEvent<Record<string, unknown>>, newValue: number) => {
		setPrimeTab(newValue);
	};
	useEffect(() => {
		if (isConfigurationPage) {
			setPrimeTab(1);
		} else if (isBranchPage) {
			setPrimeTab(2);
		} else if (isFeedbackConfigPage) {
			setPrimeTab(3);
		} else {
			setPrimeTab(0);
		}
	}, [location]);

	return (
		<StandardPageLayout>
			<div className="profile">
				{(!isHidden || unreadCasesQuantity === 0) && (
					<Helmet>
						<title>KENNEKT | User profile</title>
					</Helmet>
				)}
				<BusinessProfileHeader />
				<div className="content">
					<div className="prime-tabs prime-tabs_no-bottom-extend">
						<CustomMuiTabs.Tabs
							scrollButtons="auto"
							variant="scrollable"
							value={primeTab}
							// @ts-ignore
							onChange={handleChangePrime}
							aria-label="simple tabs example"
						>
							<CustomMuiTabs.Tab
								label={t('business_profile')}
								component={Link}
								to={url}
								{...a11yProps(0)}
							/>
							<CustomMuiTabs.Tab
								label={t('configuration')}
								component={Link}
								to={`${url}/configuration`}
								{...a11yProps(1)}
							/>
							<CustomMuiTabs.Tab
								label={t('branch_list')}
								component={Link}
								to={`${url}/branch-list`}
								{...a11yProps(2)}
							/>
							<CustomMuiTabs.Tab
								label={t('feedback_configuration')}
								component={Link}
								to={`${url}/feedback-config`}
								{...a11yProps(3)}
							/>
						</CustomMuiTabs.Tabs>
					</div>
					<div className="business-profile-tab-content">
						<Suspense fallback={<Loader tiled />}>
							<Switch>
								<Route path={path} exact component={BusinessProfilePage} />
								<Route path={`${path}/configuration`} exact component={Configuration} />
								<Route path={`${path}/branch-list`} exact component={Branches} />
								<Route
									path={`${path}/feedback-config`}
									exact
									component={FeedbackConfigurationPage}
								/>
							</Switch>
						</Suspense>
					</div>
				</div>
			</div>
		</StandardPageLayout>
	);
}

export function BusinessProfileMain(props: Props) {
	return (
		<ErrorBoundary>
			<BusinessProfileMainInner {...props} />
		</ErrorBoundary>
	);
}
