import React from 'react';
import clsx from 'clsx';
import { useAuth } from 'modules/auth/hooks/useAuth';
import dayjs from 'dayjs';
import { useAppState } from '../../../shared/state';
import { useTranslation } from 'react-i18next';
import { convertToLocalizationKey } from 'shared/services/utils/convertToLocalizationKey';

export function BusinessProfileHeader() {
	const { isDarkMode } = useAppState();
	const { t } = useTranslation();
	const { company } = useAuth();
	const companyIcon = company?.company?.icon;
	const companyIconDark = company?.company?.icon_dark;
	const companyName = company?.company.name;
	const companyStatus = company?.company.status;

	const iconUrl = isDarkMode
		? companyIconDark
			? companyIconDark?.thumbnail
			: companyIcon?.thumbnail
		: companyIcon?.thumbnail;

	return (
		<div className="profile__header mb-24 h-40 w-full rounded-xl bg-user-profile p-4 text-white lg:mb-20">
			<div className="profile__header-heading text-2xl font-medium ">{t('business_profile')}</div>
			<div className="profile__header-info  relative mx-2  mt-6 overflow-hidden rounded-2xl p-5 shadow-xl lg:mt-12">
				<div className="blurred absolute inset-0 z-0 bg-white/80 backdrop-blur-sm dark:bg-kdark/40" />
				<div className="profile__header-info-inner relative z-10 items-center justify-between lg:flex">
					<div className="profile__header-user mb-4 flex items-center md:mb-0">
						<div
							className={clsx(
								'profile__header-user-img flex-centered h-20 w-20 overflow-hidden rounded-2xl shadow-xl',
								{ 'bg-kgrey-inputBd': !companyIcon }
							)}
						>
							{companyIcon ? (
								<img src={iconUrl} alt="Business logo" className="object-fit h-full w-full" />
							) : (
								<p className="text-5xl text-kgrey">{`${companyName?.[0] || ''}`}</p>
							)}
						</div>
						<div className="profile__header-user-name  ml-5 ">
							<div className="text-2xl font-semibold text-kdark dark:text-white">{companyName}</div>
							<div className="mt-1 text-base text-kgrey">
								<span>{dayjs(company?.company.created_date).format('MMMM D, YYYY')}</span> |{' '}
								<span>Business ID: {company?.company?.id}</span>
							</div>
						</div>
					</div>
					<div
						className="profile__header-status flex items-center
					rounded-xl bg-primary py-2 px-4 text-base font-semibold uppercase leading-none"
					>
						<div className="profile__header-status-icon relative mr-1.5 h-3 w-3 rounded-full bg-white/40" />
						{t(convertToLocalizationKey(companyStatus as string))}
					</div>
				</div>
			</div>
		</div>
	);
}
